  }




  @-webkit-keyframes IconSprite {
    0% {
      -webkit-transform: scale(0.8);
      opacity: 0.5;
    }
    50% {
      opacity: 1;
      -webkit-transform: scale(1.1);
    }
    100% {
      -webkit-transform: scale(0.8);
      opacity: 0.5;
    }
  }
  @keyframes IconSprite {
    0% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      opacity: 0.5;
    }
    50% {
      -webkit-transform: scale(1.1);
