.slider-technology {
    background:  var(--background--color) url(https://hectoricardom.github.io/img/bg-gradient.png) repeat-x 0 0;    
    overflow: hidden;
    position: relative;
    width: 100%;
    padding-top: 60px;
    opacity: 0;
    -webkit-transform: scale(0.4);
    transform: scale(0.4);
    -webkit-transition: opacity .6s ease-in-out, -webkit-transform .5s ease-out;
    transition: opacity .6s ease-in-out, -webkit-transform .5s ease-out;
    transition: opacity .6s ease-in-out, transform .5s ease-out;
    transition: opacity .6s ease-in-out, transform .5s ease-out, -webkit-transform .5s ease-out;
}


.slider-technology[is-in-viewport=false]{
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transition-delay: 0.312s;
    transition-delay: 0.312s;
}

.slider-technology--slides {
    margin: 0 0 10px;
    position: relative;
}


.slider-technology--slides>li {
    background: transparent no-repeat right 0;
    left: 0;
    list-style: none;
    opacity: 0;
    position: absolute;
    top: 0;   
    width: 100%;
    visibility: hidden;
    -webkit-transition: opacity 1.2s ease,visibility .02S;
    -webkit-transition: opacity 1.2s ease, visibility .02S;
    transition: opacity 1.2s ease, visibility .02S;
}




.slider-technology--slides>li.active {
    opacity: 1;
    z-index: 7;
    visibility: visible;
    
}



.slider-technology .grid-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    max-width: 74%;
    margin: 0 auto;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}




.slider-technology--slides>li p {
    font-size: 1.375rem;
    font-weight: 300;
    color: var(--tip__card--p-color--);
}


.slider-technology--slides>li a {
    color: var(--color-base--hover);
    cursor: pointer;
    text-decoration: none;
    -webkit-transition: all 100ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: all 100ms cubic-bezier(0.25, 0.1, 0.25, 1);
}


.slider-technology--slides>li img {    
    max-width: calc(100% / 0.9);    
    -webkit-transform: translateY(45%);    
            transform: translateY(45%);
}



.slider-technology--controls.slider-technology--controls__enabled{
    display: none;
}

.slider-technology--slide-text {
