/*
--color-base--hover:
*/




.headerMobileLogo {
    background-color: var(--color-base--hover);
    height: 120px;
    position: relative;
    
}

.headerMobileLogo .logo {
    bottom: 20px;
    left: 20px;
    margin: 0;
    position: absolute;
    top: auto;
    width: auto;
    color: #fff;
}

.headerMobileLogo svg {
    fill: var(--color-logo_);
    fill-rule:nonzero;
    bottom: -20px;
    left: 20px;
    margin: 0;
    position: absolute;
    top: auto;
    width: 200px;
}


/*****************************************************  NAV    *****************************************************************/





.toggle-theme{
    position: relative;
    padding: 18px 15px 18px 0;
    text-align: center;
    width: 24px;
    height: 24px;  
    cursor: pointer;
}

.toggle-theme svg{
    fill: var(--fill--theme--color);
}



/*****************************************************  TAB INDICATOR    *****************************************************************/




.c-tabs-nav__indicator {
    background: var(--color-base--hover);
    bottom: 0;
    display: block;
    height: 4px;
    left: 0;
    position: absolute;
    -webkit-transition: left, width;
    transition: left, width;
    -webkit-transition-duration: 300ms;
    transition-duration: 300ms;
    -webkit-transition-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
    transition-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
}





/*****************************************************  NAV    *****************************************************************/


.left_Header .c-toggle {
    background-color: transparent;
    border: 0;
    display: none;
    height: 14px;
    line-height: normal;
    margin: 20px;
    padding: 0;
    cursor: pointer;
    outline: none;
}

.left_Header {
    will-change: transform;
}

.left_Header .navBox {
    float: right;
    display: flex;
    position: relative;
}

.left_Header .navBox [role='menubar'] {
    margin: 2px 75px 2px 0;
}

.navBox [role='menubar'] {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.navBox [role='menubar'] {
    list-style: none;
    margin: 0;
    padding: 0;
}


.navBox [role='menuitem'] {
    font-size: 14px;
    font-size: .875rem;
    letter-spacing: -.56px;
    letter-spacing: -.035rem;
    color: var(--tab--nav-Color--);
    cursor: pointer;
    display: block;
    font-weight: normal;
    line-height: 1.35;
    padding: 20px;
    text-transform: uppercase;
    outline: none;
}

.navBox [role='menuitem']:active, .navBox [role='menuitem']:focus, .navBox [role='menuitem']:hover {
    color: var(--color-base--hover);
    text-decoration: none;
}

/*****************************************************  NAV ON SLIDE MENU    *****************************************************************/

.navSlideBox [role='slide_item'] {
    font-size: 14px;
    font-size: .875rem;
    letter-spacing: -.56px;
    letter-spacing: -.035rem;
    color: var(--tab--nav-Color--);
    cursor: pointer;
    display: block;
    font-weight: normal;
    line-height: 1.35;
    padding: 20px;
    text-transform: uppercase;
    outline: none;
}

.navSlideBox [role='menubar'] {
    list-style: none;
    margin: 0;
    padding: 0;
}


